import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import postlist from '../components/postlist'
import Article from '../components/article'

Vue.use(Router)

export default new Router({
  routes: [
    {
      name:'root',
      path:'/',
      components: {
        main: postlist,
      }
    },
      
    {
      name:'post_content',
      path:'/topic/:id',
      components: {
        main: Article,
      }
    },
   
  ]
})



